body {
  margin: 0;
  padding: 0;
  background-image: linear-gradient(350deg, pink, #78ffd6);
  color: #020202;
  font-family: "Montserrat", sans-serif;
}

.icon {
  vertical-align: middle;
  line-height: 1px;
  font-size: 20px;
}
.icon_header {
  vertical-align: middle;
  line-height: 1px;
  font-size: 26px;
}
.icon,
.icon_header {
  margin-right: 5px;
  color: #000;
}

.close_icon {
  color: black;
  margin-left: 30px;
  margin-top: 10px;
  cursor: pointer;
}

.grid-container {
  display: grid;
  grid-template-columns: 260px 1fr 1fr 1fr;
  grid-template-rows: 0.2fr 3fr;
  grid-template-areas:
    "sidebar header header header"
    "sidebar main main main";
  height: 100vh;
  width: 100vw;
}

/* Header  */
.header {
  grid-area: header;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 30px 0 30px;
  box-shadow: 0 6px 20px -3px rgba(0, 0, 0, 0.35);
}

.menu-icon {
  display: none;
}
/* End Header  */

/* Sidebar  */
#sidebar {
  grid-area: sidebar;
  height: 100%;
  background-image: linear-gradient(45deg, #6af4fb, #e208db);
  overflow-y: auto;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
}

.sidebar-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 30px 0px 30px;
  margin-bottom: 30px;
}

.sidebar-title > span {
  display: none;
}

.sidebar-brand {
  margin-top: 15px;
  font-size: 20px;
  font-weight: 700;
}

.sidebar-list {
  padding: 0;
  list-style-type: none;
}
.sidebar-list > a {
  text-decoration: none;
}
.sidebar-list-item {
  padding: 20px 35px 20px;
  font-size: 18px;
}

.sidebar-list-item:hover {
  background-color: #78ffd7f1;
  cursor: pointer;
}

.sidebar-list-item {
  text-decoration: none;
  color: #9ea4a0;
}
.sidebar-list .sidebar-list-item {
  color: #000000;
  font-weight: 600;
  font-size: larger;
  text-decoration: none;
}

.sidebar-responsive {
  display: inline !important;
  position: absolute;
  /*
    we want the z-index of the sidebar higher so that
    the charts are not showing over the sidebar 
    on small screens
  */
  z-index: 12 !important;
}

/* End Sidebar  */

/* Main  */
.main-container {
  grid-area: main;
  overflow-y: auto;
  padding: 20px 20px;
  color: #070707f2;
}

.main-title {
  display: flex;
  justify-content: space-between;
}

.main-cards {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
  margin: 25px 0;
}
.main-cards a {
  text-decoration: none;
}
.card {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 10px 15px;
  border-radius: 5px;
}

.card {
  height: 200px;
  border-radius: 20px;
  border: 0px;
  margin: 50px;
  transition: 0.3s all ease-in-out;
  background-image: linear-gradient(45deg, #6afbc8, #e208db);
}
.card:hover {
  scale: 1.1;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}

.main-cards a h1 {
  text-align: center;
}
.card-inner {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.card-inner > .card_icon {
  font-size: 25px;
}



/* End Main  */

/* Medium <= 992px */

@media screen and (max-width: 992px) {
  .grid-container {
    grid-template-columns: 1fr;
    grid-template-rows: 0.2fr 3fr;
    grid-template-areas:
      "header"
      "main";
  }

  #sidebar {
    display: none;
  }

  .menu-icon {
    display: inline;
  }

  .sidebar-title > span {
    display: inline;
  }
}
/* Small <= 768px */

@media screen and (max-width: 768px) {
  .main-cards {
    grid-template-columns: 1fr;
    gap: 10px;
    margin-bottom: 0;
  }

  .charts {
    grid-template-columns: 1fr;
    margin-top: 30px;
  }
}

/* Extra Small <= 576px */

@media screen and (max-width: 576px) {
  .hedaer-left {
    display: none;
  }
}

.btn-space {
  margin-right: 10px;
}
.backgrounf-form{
  background-color: rgba(235, 235, 235, 0.61);
  border: 1px solid blue;
}
.calander-bg{
  background-color: #6af4fb;
}